import axios from 'axios';
import { cacheAdapterEnhancer } from 'axios-extensions';

const http = axios.create({
  baseURL: 'https://api.themoviedb.org',
  headers: { 'Cache-Control': 'no-cache' },
  // cache will be enabled by default
  adapter: cacheAdapterEnhancer(axios.defaults.adapter),
});

export default class MovieAPiServer {
  constructor() {
    this.searchQuery = '';
    this.pageCounter = 1;
    this.API_KEY = 'a1157fee69973f579feaed0c038c358a';
    this.movieId = null;
    this.lang = '';
    // 
    // this.pageSearch = 1;
    this.maxPages = null;
    this.isLoadGenres = true;
  }

  //     

  async fetchTopMovies() {
    const URL = `/3/trending/movie/week?api_key=${this.API_KEY}&page=${this.pageCounter}&language=${this.language}`;
    try {
      const response = await http.get(URL);
      //
      this.maxPages = response.data.total_pages;
      // ---
      return response.data.results;
    } catch (error) {
      return error;
    }
  }

  //   ,     
  async getGenresList() {
    const URL = `/3/genre/movie/list?api_key=${this.API_KEY}&language=${this.language}`;
    try {
      const response = await http.get(URL);
      const genresList = response.data.genres;
      localStorage.setItem('genresList', JSON.stringify(genresList));
    } catch (error) {
      return error;
    }
  }
  // ----
  async fetchMovieById() {
    const URL = `/3/movie/${this.movieId}?api_key=${this.API_KEY}&language=${this.language}`;

    try {
      const response = await http.get(URL);

      return response.data;
    } catch (error) {
      return error;
    }
  }

  async fetchMovieByQuery() {
    const URL = `/3/search/movie?api_key=${this.API_KEY}&page=${this.pageCounter}&query=${this.searchQuery}&language=${this.language}`;

    try {
      const response = await http.get(URL);
      return response.data;
    } catch (error) {
      return error;
    }
  }

  get id() {
    return this.movieId;
  }

  set id(newMovieId) {
    return (this.movieId = newMovieId);
  }

  get page() {
    return this.pageCounter;
  }
  set page(newPage) {
    return (this.pageCounter = newPage);
  }

  get query() {
    return this.searchQuery;
  }

  set query(newQuery) {
    return (this.searchQuery = newQuery);
  }
  get language() {
    return this.lang;
  }

  set language(language) {
    localStorage.setItem('language', JSON.stringify(language));
    return (this.lang = language);
  }
}
